<template>

	<view class="content">
		<!-- #ifdef APP-PLUS -->
		<view id="mapview" style=""></view>
		<!-- #endif -->
		<!-- #ifdef MP-WEIXIN -->
		<map :latitude="latitude" :longitude="latitude"></map>
		<!-- #endif --> 
		<!-- 定位与回收 -->
		<view class="top_header">
			<view class="adress_position" @click="showPicker()">
				<image class="position_icon" src="../../static/dinwei.png" mode="aspectFill"></image>
				<view class="adress_text">{{district}}</view>
			</view>
			<view class="btn-view">
				<view class="recover-btn" @click="navTo('/pages/createdOrder/chooseType')">
					预约回收
				</view>
				<view class="cart_img" @click="toCart('/pages/cart/cart')">
					<image class="cart_icon" src="../../static/huishouche.png" mode="aspectFill"></image>
					<view class="red_number" v-if="cartNumber > 0">{{cartNumber}}</view>
				</view>
			</view>
		</view>
		<!-- 定位详情 -->
		<view class="position_detail">
			<view class="loading-view" v-if="loading">
				<view class="loading"> </view>
				加载中...
			</view>
			<view class="company_view" v-if="!loading && hasData">
				<view class="company_name">
					{{siteData.shop_name}}
				</view>
				<view class="address">
					{{siteData.address}}
				</view>
				<view class="distance">
					<view class="distance_tips">距离</view>
					<view class="distance_number">
						{{siteData.distance > 1 ? `${siteData.distance.toFixed(1)}KM`: `${siteData.distance.toFixed(3)*1000}M`}}
					</view>
				</view>
				<view class="category">
					<view class="category_item" v-for="(item,index) in siteData.order_take" :key="index">
						{{item}}
					</view>
				</view>
			</view>
			<view class="nodata-tips" v-if="!loading &&!hasData">
				附近没有回收点，您可以预约上门回收
			</view>
			<view class="position-btn" id="pantBtn" @click="getMyLocation">
				<image class="position-img" src="../../static/map/recovery.png" mode="aspectFill"></image>
			</view>
			<!-- <view class="subscribe_btn">
				预约回收
			</view> -->
		</view>
		<view class="position-picker">
			<!-- <u-picker mode="region" v-model="picker_show" title="定位失败，请选择一个城市" @confirm="confirm" @cancel="cancel"
				:area-code='["44", "4408", "440803"]' cancel-color="#999999" confirm-color="#F7A300" :params="params">
			</u-picker> -->
			<!-- <u-picker mode="multiSelector" v-model="picker_show" :default-selector='default_select' :range="arrDate" range-key="label" @confirm="bindDateChange" @columnchange="columnchange">
			</u-picker> -->
		</view>
	</view>
</template>

<script>
	// #ifdef H5
	import amap from '../../js_sdk/amap_js_sdk/amap.js'
	//#endif
	export default {
		data() {
			return {
				cartNumber: 0,
				// isShowLocation: false,
				// picker_show: false,
				// params: {
				// 	province: true,
				// 	city: true,
				// 	area: true
				// },
				district: '正在获取定位',
				loading: false,
				hasData: false,
				latitude: 22.229158, //中心纬度
				longitude: 110.399284, //中心经度
				mylatitude:'', //我的维度
				mylongitude:'', // 我的经度
				// scale: 5, //最小数，缩放范围最大，可见程度最大
				// scale: 18, //最大数，缩放范围最小，可见程度最小
				// markers: [],
				markerlist: [],
				map: null, //创建地图的实例
				windowHeight: null,
				datalist: [], //站点数据列表
				siteData: {}, //站点详情
				tapsite: '', //节流，防止多次点击同一个标记点的标志

				// arrDate:[[],[]],
				// default_select:[0,0],
				// citylist:[], //城市列表
				// arealist:[],//区县列表
				// c_city:'湛江市'
			}
		},
		onShow() {
			// #ifdef H5
			
			if (uni.getStorageSync('isLogin')) { //回收车数目
				this.getCartNumber()
			}
			console.log(uni.getStorageSync('positionStorage'));

			let pickerPosition = uni.getStorageSync('pickerPosition')
			let positionStorage = uni.getStorageSync('positionStorage')
			if (pickerPosition) { //有手动选择定位
				if (positionStorage) { //有手动选择定位也有自动定位
					this.latitude = pickerPosition.latitude //中心经度
					this.longitude = pickerPosition.longitude //中心纬度
					this.mylatitude = positionStorage.latitude //我的经度
					this.mylongitude = positionStorage.longitude //我的纬度
					this.district = pickerPosition.area
					this.$nextTick(() => {
						this.map = new AMap.Map('mapview', {
							zoom: 16, //缩放级别
							center: [this.longitude, this.latitude]
						});
					})
				} else { //有手动选择定位没有自动定位
					this.latitude = pickerPosition.latitude //中心经度
					this.longitude = pickerPosition.longitude //中心纬度
					this.mylatitude = '' //我的经度
					this.mylongitude = '' //我的纬度
					this.district = pickerPosition.area
					this.$nextTick(() => {
						this.map = new AMap.Map('mapview', {
							zoom: 16, //缩放级别
							center: [this.longitude, this.latitude]
						});
					})
				}
			} else {
				if (positionStorage) { //如果没有手动选择定位，有自动获取定位
					this.latitude = positionStorage.latitude
					this.longitude = positionStorage.longitude
					this.mylatitude = positionStorage.latitude
					this.mylongitude = positionStorage.longitude
					this.district = positionStorage.area
					this.$nextTick(() => {
						this.map = new AMap.Map('mapview', {
							zoom: 16, //缩放级别
							center: [this.longitude, this.latitude]
						});
					})
				} else { //如果没有手动选择定位，没有自动获取定位,给默认的
					this.latitude = this.latitude
					this.longitude = this.longitude
					this.mylatitude = this.latitude
					this.mylongitude = this.longitude
					this.district = '获取定位失败,手动选择定位'
					this.$nextTick(() => {
						this.map = new AMap.Map('mapview', {
							zoom: 16, //缩放级别
							center: [this.longitude, this.latitude]
						});
					})
				}
			}
			
			// #endif
			this.getBusineesLocations(this.latitude, this.longitude) //获取站点经纬度列表
		},
		methods: {
			createdMarker() {
				var templist = []
				console.log(this.datalist);
				this.datalist.map(item => {
					let siteId = null
					let array = this.datalist.slice(1) //把我的位置对象移除
					console.log(array);
					let temp = array[0].distance
					array.map(item => {
						if (item.distance <= temp) {
							temp = item.distance
							siteId = item.id //找出距离最小的站点id
						}
					})
					console.log(siteId);
					if (item.id == 0) {
						var homeicon = new AMap.Icon({ //创建图标实例
							size: new AMap.Size(40, 50), // 图标尺寸
							image: '../../static/map/location.png', // Icon的图像
							// imageOffset: new AMap.Pixel(0, -60),  // 图像相对展示区域的偏移量，适于雪碧图等
							imageSize: new AMap.Size(40, 40) // 根据所设置的大小拉伸或压缩图片
						});
						var marker = new AMap.Marker({
							position: new AMap.LngLat(item.longitude, item
							.latitude), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
							title: 'wowwo',
							id: item.id,
							icon: homeicon
						});
					} else if (item.id !== siteId) { //最近的站点，图标为橙色
						this.tapsite = item.id
						var buleicon = new AMap.Icon({ //创建图标实例
							size: new AMap.Size(40, 50), // 图标尺寸
							image: '../../static/map/bule_location.png', // Icon的图像
							// imageOffset: new AMap.Pixel(0, -60),  // 图像相对展示区域的偏移量，适于雪碧图等
							imageSize: new AMap.Size(30, 30) // 根据所设置的大小拉伸或压缩图片
						});
						var marker = new AMap.Marker({
							position: new AMap.LngLat(item.longitude, item
							.latitude), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
							id: item.id,
							icon: buleicon
						});
					} else {
						console.log(item.id);
						var orangeicon = new AMap.Icon({ //创建图标实例
							size: new AMap.Size(40, 50), // 图标尺寸
							image: '../../static/map/orange_location.png', // Icon的图像
							imageSize: new AMap.Size(30, 30) // 根据所设置的大小拉伸或压缩图片
						});
						var marker = new AMap.Marker({
							position: new AMap.LngLat(item.longitude, item
							.latitude), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
							id: item.id,
							icon: orangeicon
						});
						this.getSiteDetail(item.id)
					}
					marker.on('click', this.tapMaker); //给标记添加点击事件
					templist.push(marker)
				})
				console.log(templist);
				this.map.clearMap();
				if(uni.getStorageSync('positionStorage')){  //如果没有定位我的位置，移除我的位置图标
					this.markerlist = templist
					this.map.add(this.markerlist);
				}else{
					this.markerlist = templist
					this.map.add(this.markerlist);
					this.map.remove(this.markerlist[0]);
				}
				
				
			},
			tapMaker(e) {
				console.log(e);
				console.log(e.target.w.id); //markerId
				if (e.target.w.id == this.tapsite) { //点击同一个标记不让请求第二次
					return;
				} else {
					this.tapsite = e.target.w.id
				}
				if (e.target.w.id == 0) {
					return;
				} //点击我的位置不做操作
				this.getSiteDetail(e.target.w.id)
				let templist2 = []
				this.datalist.map(item => {
					if (item.id == 0) {
						var homeicon = new AMap.Icon({ //创建图标实例
							size: new AMap.Size(40, 50), // 图标尺寸
							image: '../../static/map/location.png', // Icon的图像
							imageSize: new AMap.Size(40, 40) // 根据所设置的大小拉伸或压缩图片
						});
						var marker = new AMap.Marker({
							position: new AMap.LngLat(item.longitude, item
							.latitude), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
							title: 'wowwo',
							id: item.id,
							icon: homeicon
						});
					} else if (item.id == e.target.w.id) { //最近的站点，图标为橙色
						var orangeicon = new AMap.Icon({ //创建图标实例
							size: new AMap.Size(40, 50), // 图标尺寸
							image: '../../static/map/orange_location.png', // Icon的图像
							imageSize: new AMap.Size(35, 35) // 根据所设置的大小拉伸或压缩图片
						});
						var marker = new AMap.Marker({
							position: new AMap.LngLat(item.longitude, item
							.latitude), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
							id: item.id,
							icon: orangeicon
						});
					} else {
						var buleicon = new AMap.Icon({ //创建图标实例
							size: new AMap.Size(30, 30), // 图标尺寸
							image: '../../static/map/bule_location.png', // Icon的图像
							imageSize: new AMap.Size(30, 30) // 根据所设置的大小拉伸或压缩图片
						});
						var marker = new AMap.Marker({
							position: new AMap.LngLat(item.longitude, item
							.latitude), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
							id: item.id,
							icon: buleicon
						});
					}
					marker.on('click', this.tapMaker); //给标记添加点击事件
					templist2.push(marker)
				})
				console.log(templist2);
				this.map.remove(this.markerlist);
				this.markerlist = templist2
				this.map.add(this.markerlist);
			},
			getSiteDetail(markerId) {
				this.loading = true
				this.$zshs.request('/web/users/recovery-classify', {
						mode: 'getBusinessInfo',
						user: 'common',
						action: 'info',
						id: markerId,
						latitude: this.latitude,
						longitude: this.longitude,
					}, 'POST')
					.then((res) => {
						console.log(res);
						let that = this
						if (res.code == 200) {
							if (res.data.code == 0) {
								setTimeout(() => {
									that.loading = false //隐藏加载动画
									that.hasData = true
								}, 400)
								that.siteData = res.data.userBs
							} else {
								that.siteData = []
							}
						} else {
							that.siteData = []
						}

					})
			},
			getBusineesLocations(lat,lng) { //获取站点经纬度
				this.$zshs.request('/web/users/recovery-classify', {
						action: 'coordinate',
						mode: 'getBusinessInfo',
						user: 'common',
						latitude: lat,
						longitude: lng,
					}, 'POST')
					.then((res) => {
						console.log(res);
						if (res.code == 200) {
							if (res.data.code == 0) {
								let ressoult = res.data.userBs
								let mylocation = [{
									distance: 0,
									id: 0,
									latitude: this.latitude,
									longitude: this.longitude,
								}]
								this.datalist = mylocation.concat(ressoult)
								this.createdMarker()
							}
						}
					})
			},
			getCartNumber() { //获取回收车红点
				this.$zshs.request('/web/users/recovery-classify', {
						mode: "getOrderRecyclingCart",
						n: uni.getStorageSync('n'),
						user: "common",
						phone_key: uni.getStorageSync('clientId'),
					}, 'POST')
					.then((res) => {
						console.log(res);
						this.cartNumber = res.data.recycling_cart.length
					})
			},
			getMyLocation() { //复位
				if(uni.getStorageSync('positionStorage')){
					this.map.panTo([this.mylongitude, this.mylatitude]);
					this.getBusineesLocations(this.mylatitude,this.mylongitude)
				}else{
					alert('获取定位失败')
				}
				
			},
			confirm(e) {
				console.log(e);
				this.district = e.area.label
			},
			cancel() {
				this.$queue.showToast('定位失败，请选择一个城市')
			},
			showPicker() {
				this.$queue.navTo('/pages/areaPicker/areaPicker')
			},
			toCart(url) {
				if (this.$queue.getData('isLogin')) {
					this.$queue.navTo(url)
				} else {
					this.$queue.navTo('/pages/public/login')
				}
			},
			navTo(url) {
				uni.navigateTo({
					url: url
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.content {
		position: relative;
		overflow: hidden;
		height: calc(100vh - 90rpx);
	}

	#mapview {
		width: 750rpx;
		height: 100%;
	}

	.top_header {
		width: 100%;
		height: 88rpx;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 32rpx;
		position: fixed;
		top: 0;
		z-index: 999;
		background-color: #FFFFFF;

		.adress_position {
			display: flex;
			align-items: center;
			height: 60rpx;
			.position_icon {
				width: 32rpx;
				height: 32rpx;
				margin-right: 8rpx;
			}

			.adress_text {
				font-size: 26rpx;
				color: #3D3D3D;
				font-weight: 400;
				white-space: nowrap;
			}
		}

		.btn-view {
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.recover-btn {
			padding: 10rpx 20rpx;
			border-radius: 30rpx;
			margin-right: 20rpx;
			background-color: #fed53b;
			font-size: 26rpx;
		}

		.cart_img {
			position: relative;
			width: 40rpx;
			height: 40rpx;
			line-height: 40rpx;
			margin-right: 10rpx;

			.cart_icon {
				width: 100%;
				height: 100%;
			}

			.red_number {
				min-width: 30rpx;
				// width: 30rpx;
				height: 30rpx;
				text-align: center;
				font-size: 24rpx;
				line-height: 30rpx;
				border-radius: 24rpx;
				color: #fff;
				background: #F76675;
				display: inline-block;
				position: absolute;
				top: -8rpx;
				right: -8rpx;
			}
		}


	}

	.map-view {
		width: 100vw;
		height: calc(100vh - 44px - 50px);
		margin-top: 88rpx;
	}

	.map {
		width: 100%;
		height: 100%;
	}

	/deep/.uni-map {
		height: 85vh;
	}

	.position_detail {
		width: 718rpx;
		max-height: 230rpx;
		position: absolute;
		bottom: 5vw;
		left: 2vw;
		padding: 4vw;
		width: 96vw;
		// height: 354rpx;
		background: #FFFFFF;
		box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.12);
		border-radius: 24rpx;

		// display: flex;
		// align-items: center;
		// justify-content: center;
		.company_view {
			display: flex;
			flex-direction: column;
		}

		.company_name {
			display: inline-block;
			font-family: PingFangSC-Medium;
			font-size: 32rpx;
			color: #3D3D3D;
			font-weight: bold;
			margin-bottom: 2vw;
		}

		.address {
			display: inline-block;
			max-width: 70vw;
			font-family: PingFangSC-Regular;
			font-size: 24rpx;
			color: #757575;
			font-weight: 400;
			margin-bottom: 2vw;
		}

		.distance {
			width: 20vw;
			display: flex;
			flex-direction: column;
			text-align: left;
			position: absolute;
			right: 0.5vw;
			top: 4vw;

			.distance_tips {
				height: 28rpx;
				font-family: PingFangSC-Regular;
				font-size: 10px;
				color: #F7A300;
				font-weight: 400;
				margin-bottom: 2vw;
			}

			.distance_number {
				height: 44rpx;
				display: inline-block;
				font-family: PingFangSC-Medium;
				font-size: 32rpx;
				color: #F7A300;
				text-align: left;
				font-weight: 600;
			}
		}

		.category {
			width: 100%;

			.category_item {
				height: 40rpx;
				font-size: 24rpx;
				padding: 0 1vw;
				display: inline-block;
				border: 2rpx solid #999999;
				border-radius: 4rpx;
				margin-right: 2vw;
				color: #999999;
			}
		}

		.nodata-tips {
			text-align: center;
			margin-top: 40rpx;
			margin-bottom: 40rpx;
		}

		.position-btn {
			width: 72rpx;
			height: 72rpx;
			border-radius: 72rpx;
			background-color: #FFFFFF;
			position: fixed;
			right: 30rpx;
			bottom: 370rpx;
			display: flex;
			align-items: center;
			justify-content: center;

			.position-img {
				width: 48rpx;
				height: 48rpx;
				border-radius: 48rpx;
			}
		}

		.subscribe_btn {
			width: 100%;
			height: 80rpx;
			background: #FFD426;
			text-align: center;
			line-height: 80rpx;
			background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.12) 0%, #F7A300 100%);
			border-radius: 44rpx;
			margin-top: 6vw;
			font-size: 32rpx;
			color: #3D3D3D;
			font-weight: bold;
		}
	}

	.loading-view {
		width: 160rpx;
		height: 150rpx;
		margin: 0 auto;
		// margin-top: 80rpx;
		color: #646464;
		// background-color: rgba(0, 0, 0, 0.6);
		border-radius: 20upx;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	/* 动画样式 */
	.loading {
		border: 10upx solid rgba(0, 0, 0, 0.01);
		border-radius: 50%;
		border-top: 6upx solid #646464;
		border-right: 6upx solid #646464;
		border-bottom: 6upx solid #646464;
		width: 30upx;
		height: 30upx;
		margin-right: 20rpx;
		-webkit-animation: spin 1.4s linear infinite;
		animation: spin 1.4s linear infinite;
	}

	@-webkit-keyframes spin {
		0% {
			-webkit-transform: rotate(0deg);
		}

		100% {
			-webkit-transform: rotate(360deg);
		}
	}

	@keyframes spin {
		0% {
			transform: rotate(0deg);
		}

		100% {
			transform: rotate(360deg);
		}
	}
</style>
